* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  background-image: url('https://monoawards.com/img2/book2.png');
  height: 50vh;
  background-size: cover;
  background-position-x: center;
  display: flex;
  flex-direction: column;
  justify-content: center; 
  align-items: center;
}
.search{
  width: 400px;
  margin: 50px;
}
.but-close {
  display: none;
}
.mainList {
  background-color: #f7f7f7;
  min-height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.cardList{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 30px 15px;
}
.card {
  width: 16rem;
  margin: 50px;
  align-items: center;
  text-align: start;
}
.card img{
  height: 250px;
}
.card p {
  color: #777;
}
.card button {
  padding: 10px;
  color: #333;
}
.addedCard {
  display: flex;
  margin-top: 50px;
}
.main {
  width: 250px;
   height: 300px;
}
.addedCard img {
   width: 230px;
   height: 300px;
   margin-right: 20px;
   margin-top: 30px;
}
.addedCard h3 {
  font-weight: 700;
  color: #333;
}
.addedCard .p {
  color: #777;
  font-weight: 600;
}
.addedCard .card-body {
  display: flex;
  flex-direction: column;
  justify-content: center;
 
}

@media screen and (max-width: 768px) {
  .cardList {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .card {
    width: 15rem;
    margin-right: 80px;
  }
  .search {
    width: 250px;
  }
  .header{
    height: 25vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-position-x: center;
  }
  
}


































